import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { DeviceDetectorService } from 'ngx-device-detector';
import { AgendamentoRetiradaDialogComponent } from './agendamento-retirada-dialog.component';
import { AgendamentoRetiradaSuccessDialogComponent } from './agendamento-retirada-success-dialog.component';

@Component({
  selector: 'movida-agendamento-retirada',
  template: ``
})
export class AgendamentoRetiradaComponent implements OnInit, OnChanges {

  @Input() show: boolean;
  @Input() showSuccess: boolean;
  @Input() pedido: any;
  @Input() servico: any;
  @Input() datas: any;
  @Output() onCloseModal = new EventEmitter<boolean>();
  private selectedSchedule: any;
  private isDeviceMobile: boolean;

  constructor(
    private dialog: MatDialog,
    private deviceService: DeviceDetectorService
  ) { }

  ngOnInit(): void {
    this.isDeviceMobile = this.deviceService.isMobile();
  }

  ngOnChanges(changes: SimpleChanges) {

    if (changes.show) {

      const showCurrentVal = changes.show.currentValue;
      const showPreviousValue = changes.show.previousValue;

      if (showCurrentVal != showPreviousValue && showCurrentVal) {
        this.openModal();
      }
    }

    if (changes.showSuccess) {

      const showSuccessCurrentVal = changes.showSuccess.currentValue;
      const showSuccessPreviousValue = changes.showSuccess.previousValue;

      if (showSuccessCurrentVal != showSuccessPreviousValue && showSuccessCurrentVal) {
        this.openSuccessModal();
      }
    }
  }

  public openModal(): void {

    const data = {
      isMobile: this.isDeviceMobile,
      ...this.servico,
      PedidoFrotaID: this.pedido.PedidoFrotaID,
      datas: this.datas
    };

    this.createModalTo(AgendamentoRetiradaDialogComponent, data).then(dialogRef => {
      dialogRef.afterClosed().subscribe((response) => {
        this.selectedSchedule = response;
        this.closeModal();
      });
    });
  }

  public closeModal(): void {
    this.show = false;
    this.onCloseModal.emit(this.selectedSchedule);
  }

  private openSuccessModal(): void {

    const data = {
      isMobile: this.isDeviceMobile,
      ...this.selectedSchedule
    };

    this.createModalTo(AgendamentoRetiradaSuccessDialogComponent, data).then(dialogRef => {
      dialogRef.afterClosed().subscribe();
    });
  }

  private createModalTo(dialogComponent: any, data: any): Promise<any> {

    const promise: Promise<MatDialogRef<typeof dialogComponent>> = new Promise((resolve) => {
      setTimeout(() => {
        resolve(this.dialog.open(dialogComponent, {
          width: '100vw',
          data: data,
          disableClose: false
        }));
      });
    });

    return promise;
  }

}
