import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AgendamentoRetiradaService } from './agendamento-retirada.service';

interface DataAgendamento {
    weekday: number;
    day: number;
    fullDate: Date;
    selected?: boolean;
    index?: number;
    show?: boolean;
    disabled?: boolean;
}

interface HoraAgendamento {
    time: string;
    selected?: boolean;
    index?: number;
    show?: boolean;
}

@Component({
    templateUrl: './agendamento-retirada-dialog.component.html',
    styleUrls: ['./agendamento-retirada-dialog.component.scss'],
    providers: [],
})
export class AgendamentoRetiradaDialogComponent implements OnInit {

    @Output() yes = new EventEmitter();
    @Output() not = new EventEmitter();
    public dateList: Array<DataAgendamento> = [];
    public hourList: Array<HoraAgendamento> = [];
    public selectedDate: Date;
    public selectedHour: HoraAgendamento;
    public month: string;
    public year: number;
    private LIMIT: number;
    public TOTAL_DATE_PAGES: number;
    public CURRENT_DATE_PAGE: number;
    public TOTAL_HOUR_PAGES: number;
    public CURRENT_HOUR_PAGE: number;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        public dialogRef: MatDialogRef<AgendamentoRetiradaDialogComponent>,
        public dialog: MatDialog,
        public agendamentoRetiradaService: AgendamentoRetiradaService
    ) { }

    ngOnInit(): void {
        this.LIMIT = this.data.isMobile ? 4 : 5;
        this.setupDate();
        this.setupHour();
    }

    public yesAccepted(): void {

        const response = {
            date: this.selectedDate.toISOString().slice(0, 10),
            time: this.selectedHour.time,
            filial: this.data.filial,
            servico_id: this.data.id,
            canal_id: this.data.canal.id,
            horario_config_id: this.data.horarios_config_id,
            PedidoFrotaID: this.data.PedidoFrotaID
        };

        this.yes.emit(response);

        this.dialogRef.close(response);
    }

    public notAccepted(): void {
        this.dialogRef.close();
        this.not.emit('false');
    }

    public clearDialog(): void {
        this.dialog.closeAll();
    }

    public previousDate(): void {

        if (this.CURRENT_DATE_PAGE > 1) {

            this.CURRENT_DATE_PAGE--;
            this.scrollParentToChild(document.getElementById('date-change'), '-');
        }
    }

    public nextDate(): void {

        if (this.CURRENT_DATE_PAGE < this.TOTAL_DATE_PAGES) {

            this.CURRENT_DATE_PAGE++;
            this.scrollParentToChild(document.getElementById('date-change'), '+');
        }
    }

    public previousTime(): void {
        this.scrollParentToChild(document.getElementById('hour-change'), '-');
    }

    public nextTime(): void {
        this.scrollParentToChild(document.getElementById('hour-change'), '+');
    }

    public selectDate(index: number): void {

        this.dateList.forEach((date, i) => {

            date.selected = (i == index);

            if (date.selected) {
                this.selectedDate = date.fullDate;
                this.month = this.agendamentoRetiradaService.getMonth(date.fullDate.getMonth());
                this.year = date.fullDate.getFullYear();
            }
        });
        this.setupHour();
    }

    public selectHour(index: number): void {

        this.hourList.forEach((hour, i) => {

            hour.selected = (i == index);

            if (hour.selected) {
                this.selectedHour = hour;
            }
        });
    }

    private setupDate(): void {

        let wasMonthAndYearUpdated: boolean;
        let hasSelected: boolean;

        this.getDates().forEach((date, i) => {

            const dateObj: Date = this.getDateObj(date.data);
            let selected: boolean = false;

            if (!hasSelected && date.intervalos.length > 0) {
                this.selectedDate = dateObj;
                selected = true;
                hasSelected = true;
            }

            const dataAgendamento: DataAgendamento = {
                weekday: dateObj.getDay(),
                day: dateObj.getDate(),
                fullDate: dateObj,
                selected: selected,
                index: i,
                show: (i < this.LIMIT),
                disabled: (date.intervalos.length == 0)
            }

            this.dateList.push(dataAgendamento);

            if (dataAgendamento.show && !wasMonthAndYearUpdated) {
                this.month = this.agendamentoRetiradaService.getMonth(dateObj.getMonth());
                this.year = dateObj.getFullYear();
                wasMonthAndYearUpdated = true;
            }
        });

        this.TOTAL_DATE_PAGES = Math.ceil(this.dateList.length / this.LIMIT);
        this.CURRENT_DATE_PAGE = 1;
    }

    private setupHour(): void {

        this.hourList.splice(0, this.hourList.length);

        this.getDates().forEach(date => {

            const dateObj: Date = this.getDateObj(date.data);
            const selected: boolean = dateObj.getTime() == this.selectedDate.getTime();

            if (selected) {
                date.intervalos.forEach((intervalo, j) => {

                    const inicio: string = intervalo.inicio;

                    const horaAgendamento: HoraAgendamento = {
                        time: inicio,
                        selected: false,
                        index: j,
                        show: (j < this.LIMIT)
                    };

                    this.hourList.push(horaAgendamento);
                });
            }
        });

        this.TOTAL_HOUR_PAGES = Math.ceil(this.hourList.length / this.LIMIT);
        this.CURRENT_HOUR_PAGE = 1;
    }

    private getDateObj(date: string): Date {

        const dateArray: Array<string> = date.split("-");

        const year = Number(dateArray[0]);
        const month = Number(dateArray[1]) - 1;
        const day = Number(dateArray[2]);

        const dateObj: Date = new Date(year, month, day);

        return dateObj;
    }

    private getDates(): Array<any> {

        const dates: Array<any> = Array.isArray(this.data.datas) ? this.data.datas : [];

        return dates;
    }

    public scrollParentToChild(parent, factor) {

        let parentRect = parent.getBoundingClientRect();

        const sizeScroll = parentRect.width;

        if (factor === '-') {
            parent.scrollLeft = parent.scrollLeft - sizeScroll;
        } else {
            parent.scrollLeft += sizeScroll;
        }

    }
}
