import { Component, HostListener, Input, OnInit } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { CarDetailService } from 'src/app/core/services/car-detail.service';
import { MainSharedService } from 'src/app/util/shared.service';
import { AlertMessageComponent } from '../../alert-message/alert-message.component';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.scss']
})
export class OrderListComponent implements OnInit {

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (window.innerWidth < 768) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }
  }

  private _order: any;
  @Input()
  set order(value: any) {
    this._order = value;
    this.setStatusDetails(value);
  }
  get order(): any {
    return this._order;
  }

  get showContract() {
    if (window.location.href.includes('/minha-conta/pedido/')) {
      return false;
    }
    return true;
  }

  public pedido: any;
  public servico: any;
  public datas: any;
  public showAgendamentoRetirada: boolean = false;
  public showAgendamentoRetiradaSuccess: boolean = false;
  public isMobile: boolean;
  public emptyReservation: boolean = false;
  public showBtnLabel = true;
  public btnLabel = 'Ver Detalhes';
  public orderMsg: string | SafeHtml = 'Seu pedido está em aberto, precisamos de alguns documentos para darmos andamento no processo.';
  public selectedSchedule: any;

  constructor(
    private router: Router,
    private sanitizer: DomSanitizer,
    private carDetailService: CarDetailService,
    private shared: MainSharedService,
    private alertMsgComponent: AlertMessageComponent
  ) { }

  ngOnInit() {
    this.onResize(null);
  }

  checkReservation() {
    if (!this.pedido.ReservaID || (!this.pedido.ReservaID.length && this.pedido.ReservaID.length === 0)) {
      this.emptyReservation = true;
      this.orderMsg = 'Não foi possível exibir os detalhes do seu pedido.';
    }
  }

  setStatusDetails(order) {
    try {
      this.pedido = order.pedido;
      this.showBtnLabel = true;

      if (localStorage.getItem('choosenView') === 'seller') {
        this.btnLabel = 'Ver Detalhes';
        this.orderMsg = 'Acompanhe o pedido do seu cliente.';
      } else {

        switch (order.pedido.status.StatusId) {
          case 0:
            this.btnLabel = 'Continuar Pedido';
            this.orderMsg = 'Clique no botão para continuar seu pedido.';
            break;
          case 1:
            this.showBtnLabel = false;
            this.btnLabel = 'Continuar Pedido';
            this.orderMsg = this.sanitizer.bypassSecurityTrustHtml('Não conseguimos validar sua <b>CNH</b> e <b>Foto</b>, mas não se preocupe, vamos repetir estas etapas na loja para liberação do veículo.');
            this.checkReservation();
            break;
          case 10: // Disponível agendamento de retirada
          case 3:  // Aprovado
          case 1:  // Aguardando documentação
            if (this.hasVehicleScheduleAvailable()) {
              this.btnLabel = 'Agendar retirada';
              this.orderMsg = 'Clique no botão para agendar a retirada.';
            } else {
              this.checkReservation();
            }
            break;
          default:
            this.btnLabel = 'Ver detalhes';
            this.orderMsg = '';
            this.checkReservation();
            break;
        }
      }
    } catch (err) {
      this.btnLabel = 'Ver Detalhes';
      this.orderMsg = 'Você pode visualizar os detalhes do seu pedido.';
    }
  }

  redirectTo() {
    try {
      if (localStorage.getItem('choosenView') === 'seller') {
        this.router.navigate([`/vendedor/pedido/detalhes/${this.pedido.PedidoFrotaID}`]);
      } else {
        switch (this.pedido.status.StatusId) {
          case 0:
            this.router.navigate([`/assinatura/pagamento/${this.pedido.PedidoFrotaID}`]);
            break;
          case 10: // Disponível agendamento de retirada
          case 3: // Aprovado
          case 1: // Aguardando documentação           
            if (this.hasVehicleScheduleAvailable()) {
              this.loadVehicleSchedulesAvailable();
            }
            break;
          default:
            this.router.navigate([`/minha-conta/meus-pedidos/${this.pedido.PedidoFrotaID}`]);
            break;
        }
      }
    } catch (error) {
      this.router.navigate([`/minha-conta/pedido/${this.pedido.PedidoFrotaID}`]);
    }
  }

  public async saveVehicleSchedule(selected: any): Promise<void> {

    this.showAgendamentoRetirada = false;
    this.selectedSchedule = selected;

    if (this.selectedSchedule) {

      const payload = {
        ...this.selectedSchedule
      };

      this.shared.setLoadingAtivo(true);

      await this.carDetailService.saveVehicleSchedule(payload).toPromise().then(response => {

        this.shared.setLoadingAtivo(false);

        if (!response.data) {
          this.showMsgAlertDialog(response.message);
        } else {
          this.showAgendamentoRetiradaSuccess = true;
        }

      });
    }
  }

  private hasVehicleScheduleAvailable(): boolean {

    return [139].includes(this.pedido.status_frota.StatusId);
  }

  private async loadVehicleSchedulesAvailable(): Promise<void> {

    this.shared.setLoadingAtivo(true);

    const filial_id = this.pedido.loja.FilialID;

    await this.carDetailService.getVehicleScheduleAvailables(1, 26, filial_id).toPromise().then(response => {

      if (response.data && response.data.servico && response.data.datas) {
        
        this.servico = response.data.servico;

        if(!this.servico.filial) {
          this.servico.filial = {
            id: this.pedido.loja.FilialID,
            nome: this.pedido.loja.Nome, 
            endereco : this.pedido.loja.Endereco
          };
        }
        
        this.datas = response.data.datas;
        this.showAgendamentoRetirada = true;
      } else {
        this.showMsgAlertDialog('Não existem horários disponíveis para agendamento.');
      }
      this.shared.setLoadingAtivo(false);
    });

  }

  private showMsgAlertDialog(message: string): void {

    this.alertMsgComponent.openDialog({
      tipo: 'message',
      titulo: 'Agendamento',
      mensagem: message
    }, 'message', true);
  }
}
