import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AgendamentoRetiradaService } from './agendamento-retirada.service';

@Component({
    templateUrl: './agendamento-retirada-success-dialog.component.html',
    styleUrls: ['./agendamento-retirada-success-dialog.component.scss'],
    providers: [],
})
export class AgendamentoRetiradaSuccessDialogComponent implements OnInit {

    public showLinkAddressCopiedSuccess: boolean;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        public dialogRef: MatDialogRef<AgendamentoRetiradaSuccessDialogComponent>,
        public dialog: MatDialog,
        private agendamentoRetiradaService: AgendamentoRetiradaService
    ) { }

    ngOnInit(): void { }

    public getFormattedDate(): string {

        const date = new Date(`${this.data.date} ${this.data.time}`);
        return `${date.getDate()} de ${this.agendamentoRetiradaService.getMonth(date.getMonth())}`;
    }

    public clearDialog(): void {
        this.dialog.closeAll();
        window.location.reload();
    }

    public copyLinkAddress(): void {

        const range = document.createRange();
        range.selectNode(document.getElementById('addressLink'));
        window.getSelection().removeAllRanges();
        window.getSelection().addRange(range);
        document.execCommand('copy');
        window.getSelection().removeAllRanges();

        this.showLinkAddressCopiedSuccess = true;

        setTimeout(() => {
            this.showLinkAddressCopiedSuccess = false;
        }, 5000);
    }

    @HostListener('window:keyup.esc')
    public onKeyUp(): void {
        this.dialogRef.close();
    }
}