import { Component, OnInit, HostListener, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { MainSharedService } from 'src/app/util/shared.service';
import { OrderService } from 'src/app/core/services/order.service';
import { OrderMetaData } from 'src/app/shared/models/order.model';
//import { StatusPedido } from 'src/app/shared/models/status-pedido.model';
import { Title } from '@angular/platform-browser';
import { IBreadcrumb } from 'src/app/shared/breadcrumb/breadcrumb.component';
import { SubSink } from 'subsink';
import { environment } from 'src/environments/environment';
import { CheckLoginService } from 'src/app/core/services/checklogin.service';
import { GtmService } from 'src/app/core/services/gtm.service';
import { SeoService } from 'src/app/core/services/header/seo.service';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.scss']
})
export class AccountComponent implements OnInit, OnDestroy {

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    for (let key in this.tabs) {
      if (window.innerWidth > 560) {
        this.tabs[key] = key == this.lastTab;
      }
    }
  }

  public lastTab = 'orders';
  public tabs = {
    orders: true,
    desires: false,
    address: false,
    infos: false
  };

  public isSeller: boolean;
  public showPagination: boolean = false;

  public user: any = {};
  public address: string = 'Nenhum endereço cadastrado';
  public orders = new Array<any>();
  public ordersMetaData: OrderMetaData;

  emptyReservation: boolean = false;

  public ordersError: string = null;

  private sub = new SubSink();

  public urlMovida = environment.siteMovida;

  public steps: IBreadcrumb[] = [
    {
      id: '0',
      label: 'Home',
      link: '/'
    },
    {
      id: '1',
      label: 'Meu portal',
      link: '/portal'
    },
    {
      id: '2',
      label: 'Meus pedidos'
    }
  ];

  constructor(
    private titleS: Title,
    private router: Router,
    private shared: MainSharedService,
    private orderService: OrderService,
    private gtm: GtmService,
    private checkLoginService: CheckLoginService,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.checkLoginService.check()
    this.titleS.setTitle('Zero Km - Área Cliente');
    this.seoService.createLinkForCanonicalURL();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - Área do Cliente');
    this.isSeller = localStorage.getItem('choosenView') === 'seller';

    this.getUser();

    this.reloadOrders(1);
  }

  getUser() {
    this.user = this.shared.user;
  }

  reloadOrders(pageNumber) {
    this.showPagination = false;
    if (this.isSeller) {
      this.getSellerOrders(pageNumber);
    } else {
      this.getOrders(pageNumber);
    }
  }

  getSellerOrders(pageNumber) {
    this.ordersError = null;
    this.shared.setLoadingAtivo(true);
    this.sub.sink = this.orderService.getSellerOrders(this.shared.user.PessoaID, pageNumber).subscribe(res => {
      if (res !== null && res !== undefined && res && res.data && res.data.length > 0) {
        this.orders = res.data;
        this.emptyReservation = false;
        this.showPagination = true;
        this.shared.setLoadingAtivo(false);
      } else {
        this.emptyReservation = true;
        this.ordersError = "Você ainda não possui pedidos.";
        this.shared.setLoadingAtivo(false);
      }
    }, ex => {
      this.ordersError = "Erro ao recuperar os pedidos. Tente novamente.";
      this.shared.setLoadingAtivo(false);
    });
  }

  getOrders(pageNumber) {
    this.ordersError = null;
    this.shared.setLoadingAtivo(true);
    this.sub.sink = this.orderService.getOrders(pageNumber).subscribe(res => {
      if (res !== null && res !== undefined && res && res.data && res.data.length > 0) {
        this.orders = res.data;
        this.emptyReservation = false;
        this.showPagination = true;
        this.shared.setLoadingAtivo(false);
      } else {
        this.emptyReservation = true;
        this.ordersError = "Você ainda não possui nenhum pedido.";
        this.shared.setLoadingAtivo(false);
      }
    }, ex => {
      this.ordersError = "Erro ao recuperar os pedidos. Tente novamente.";
      this.shared.setLoadingAtivo(false);
    });
  }

  toggleTab(tab: string) {
    this.lastTab = tab;
    if (window.innerWidth > 560) {
      for (let key in this.tabs) {
        this.tabs[key] = key == tab;
      }
    } else {
      this.tabs[tab] = !this.tabs[tab];
    }
  }

  redirectTo(route) {
    this.router.navigate([`/${route}`], { queryParamsHandling: 'merge' });
  }

  public redirectToMovida(route): void {
    const url = `${this.urlMovida}${route}`;
    window.open(url);
  }


  logout() {
    this.shared.removeItensLogout()
    this.shared.setUsuarioLogado();
    this.router.navigate(['/']);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}
